window.onclick = function(e){
  if(!e.target.matches('.dropbtn')){
      var file = document.getElementById("file");
      var edit = document.getElementById("edit");
      var find = document.getElementById("find");
      var view = document.getElementById("view");
      var tools = document.getElementById("tools");
      var terminal = document.getElementById("terminal");
      var help = document.getElementById("help");

      if(file.classList.contains('drop-it')){
          file.classList.remove('drop-it');
      }


      if(edit.classList.contains('drop-it')){
          edit.classList.remove('drop-it');
      }



      if(view.classList.contains('drop-it')){
          view.classList.remove('drop-it');
      }


      if(tools.classList.contains('drop-it')){
          tools.classList.remove('drop-it');
      }

      if(terminal.classList.contains('drop-it')){
          terminal.classList.remove('drop-it');
      }


      if(help.classList.contains('drop-it')){
          help.classList.remove('drop-it');
      }
  }
}


