function Note(){
  var notet=document.getElementById("notet");
  
  localStorage.setItem("notet", notet.value);
}

function center(){
  var notet=document.getElementById("notet");
  var cnt=notet.style.textAlign="center";
  localStorage.setItem("center", cnt)
  
}


function left(){
  var notet=document.getElementById("notet");
  var lft=notet.style.textAlign="left"
  localStorage.setItem("left",lft);
  
}

function right(){
  var notet=document.getElementById("notet");
 var rht= notet.style.textAlign="right"
  localStorage.setItem("right",rht)
}

function justify(){
  var notet=document.getElementById("notet");
  var jus =notet.style.textAlign="justify"
  localStorage.setItem("justify",jus)
}

function Load(){
  var notet=document.getElementById("notet");
 
notet.value=localStorage.getItem("notet");
if(center()){
notet.style.textAlign=localStorage.getItem("cnt");
}
if(left()){
  notet.style.textAlign=localStorage.getItem("lft")
  }
  if(right()){
    notet.style.textAlign=localStorage.getItem("rht")
    }
    if(justify()){
      notet.style.textAlign=localStorage.getItem("jus")
      }
}


